import "../../scss/layout/index.scss";
import $ from "jquery";

function navEventBind() {
    let $navContainer = $(".nav-container");
    /**
     * 多级菜单
     */
    $(".nav .nav-dd").on("mouseover",
        function (e: any) {
            let $this = $(this);
            if (!$this.hasClass("open")) {
                let $dropdownMenu = $(this).children(".nav-dd-menu");
                $dropdownMenu.addClass("active");
            }
        }).on("mouseout",
        function (e: any) {
            let $this = $(this);
            if (!$this.hasClass("open")) {
                let $dropdownMenu = $(this).children(".nav-dd-menu");
                $dropdownMenu.removeClass("active");
            }
        }).on("click",
        function (e: any) {
            let $this = $(this);
            if ($this.hasClass("open")) {
                $this.removeClass("open");
            } else {
                $this.addClass("open");
                let $dropdownMenu = $(this).children(".nav-dd-menu");
                $dropdownMenu.addClass("active");
            }
        });

    $(".nav-toggle", $navContainer).on("click",
        (e: any) => {
            let $menu = $(".nav-menu", $navContainer);
            if (!$menu.hasClass("nav-show")) {
                $menu.addClass("nav-show")
                    .removeClass("nav-hide");
            } else {
                $menu.removeClass("nav-show")
                    .addClass("nav-hide");
            }
        });

    //搜索框显示
    $(".search-container>a", $navContainer).on('click',
        (e: any) => {
            let $searchBox = $(".search-container .search-box", $navContainer);
            if ($searchBox.hasClass("active")) {
                $searchBox.removeClass("active");
            } else {
                $searchBox.addClass("active");
            }
        });

    //判断那个导航栏高亮
    var s = $("title").text().split("-")[0].replace(" ", "");
    var navArr = ["首页", "已往内容", "点点滴滴", "关于本站"];
    var isDefault = true;
    for (var i = 0; i < navArr.length; i++) {
        if (s.indexOf(navArr[i]) > -1) {
            $("#zxyNav li:contains(" + s + ")").addClass("active");
            isDefault = false;
            break;
        }
    }
    if (isDefault) {
        $("#zxyNav .active").removeClass("active");
    }


    let defaultHref = '/';
    let homeHref = '/home/index';

    function checkHighLight() {
        let pathname = location.pathname.toLocaleLowerCase();
        if (!pathname) {
            pathname = defaultHref;
        }
        let pathParamter = pathname.split('/')
            .filter((e) => {
                return e;
            });
        if (pathParamter.length === 1) {
            pathParamter.push('index');
        }
        pathname = '/' + pathParamter.join('/');
        if (pathname === homeHref) {
            pathname = defaultHref;
        }
        $('li.active', $navContainer).removeClass('active');

        $('.nav-link', $navContainer)
            .each((index: number, htmlEle: HTMLElement) => {
                let href = $(htmlEle).attr('href');
                if (href) {
                    href = href.toLowerCase();
                    if (pathname === href) {
                        let $li = $(htmlEle).parent('li');
                        $li.addClass('active');
                        $li.parents('.nav-dd').addClass('active');
                        return false;
                    }
                }
                return;
            });

    }

    checkHighLight();

    //搜索事件
    $(".search-container .btn-search-submit", $navContainer).on("click",
        (e:any) => {
            e.preventDefault();
            var oq = <string>$(".search-val").val();
            var searchVal = oq;
            if (!searchVal) {
                return;
            } else {
                var queryVals = [];
                var reParten = /"[\S]*"/g;
                var result = reParten.exec(searchVal);
                while (result) {
                    queryVals.push(result[0]);
                    searchVal = searchVal.replace(result[0], "");
                    result = reParten.exec(searchVal);
                }
                searchVal = searchVal.trim().replace(/\s+/g, ' ');;
                var searchOtherAndVals = searchVal.split(' ');
                queryVals = queryVals.concat(searchOtherAndVals);
                for (var i = 0; i < queryVals.length; i++) {
                    queryVals[i] = 'q=' + queryVals[i];
                }
                
                window.location.href = "/Essays/Search?oq=" + oq + "&" + queryVals.join('&');
            }
        });
}

navEventBind();




